import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom'; 
import axios from 'axios';
import logo from '../../assets/images/petppo_1.svg'; 
import './LoginScreen.css'; 

function LoginScreen() {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const navigate = useNavigate();

  const handleLogin = async (e) => {
    e.preventDefault(); //    
    try {
      const response = await axios.post('https://2352-210-119-237-59.ngrok-free.app/api/user/login', {
        email: email,
        password: password,
      });

      if (response.data.success) {
        const token = response.data.token;
        localStorage.setItem('token', token); // JWT    
        alert(' !');
        navigate('/dashboard'); 
      } else {
        //     
        alert('   .');
      }

    } catch (error) {
      //     
      alert('  .  .');
      console.error('Login error:', error);
    }
  };

  return (
    <div className="login-container">
      <img src={logo} alt="Petppo Logo" className="logo" />
      <h2 className="login-title">  </h2>
      <form className="login-form" onSubmit={handleLogin}>
        <input
          type="email"
          placeholder=""
          required
          className="input-field"
          value={email}
          onChange={(e) => setEmail(e.target.value)} //   
        />
        <input
          type="password"
          placeholder=""
          required
          className="input-field"
          value={password}
          onChange={(e) => setPassword(e.target.value)} //   
        />
        <button type="submit" className="login-button"></button>
      </form>
    </div>
  );
}

export default LoginScreen;
