import React, { useState } from 'react';
import './UserManageScreen.css';
import Sidebar from '../../components/Sidebar';
import TableComponent from '../../components/UserTableComponent';
import PaginationComponent from '../../components/PaginationComponent';
import Modal from '../../components/Modal';
import ConfirmModal from '../../components/ConfirmModal';

function UserManageScreen() {
  const [users, setUsers] = useState([
    { id: '001', name: '', email: 'wjdgmldus28@naver.com', password: 'hy23!' },
    { id: '002', name: '', email: 'wjdgmldus28@naver.com', password: 'hy23!' },
    { id: '003', name: '', email: 'wjdgmldus28@naver.com', password: 'hy23!' },
    //  ...
  ]);

  const [currentPage, setCurrentPage] = useState(1);
  const usersPerPage = 12;
  const totalPages = Math.ceil(users.length / usersPerPage);

  //    
  const indexOfLastUser = currentPage * usersPerPage;
  const indexOfFirstUser = indexOfLastUser - usersPerPage;
  const currentUsers = users.slice(indexOfFirstUser, indexOfLastUser);

  //   
  const handlePageChange = (pageNumber) => {
    setCurrentPage(pageNumber);
  };

  //   
  const [modalVisible, setModalVisible] = useState(false);
  const [modalContent, setModalContent] = useState('');
  const [confirmModalVisible, setConfirmModalVisible] = useState(false);
  const [confirmModalMessage, setConfirmModalMessage] = useState('');
  const [modalActions, setModalActions] = useState({
    text1: '',
    text2: '',
    onCancel: () => {},
    onConfirm: () => {},
  });

  //   
  const handleDelete = (userId) => {
    const filteredUsers = users.filter((user) => user.id !== userId);
    setUsers(filteredUsers);
    setModalVisible(false); //    
  };

  //   
  const columns = [' ID', '', '', '', ' '];

  //  
  const handleDeleteClick = (postId) => {
    setModalContent('  ?');
    setModalActions({
      text1: '',
      text2: '',
      onCancel: () => setModalVisible(false),
      onConfirm: () => confirmDelete(postId), //    
    });
    setModalVisible(true);
  };

  //    
  const confirmDelete = (postId) => {
    handleDelete(postId);
    setConfirmModalMessage('  !');
    setConfirmModalVisible(true);
  };

  //     
  const handleCloseConfirmModal = () => {
    setConfirmModalVisible(false);
  };

  return (
    <div className="user-manage-container">
      <Sidebar />
      <main className="user-manage-content">
        <h2 className="user-manage-title"> </h2>
        <TableComponent columns={columns} data={currentUsers} onDelete={handleDeleteClick} />
        <PaginationComponent
          currentPage={currentPage}
          totalPages={totalPages}
          onPageChange={handlePageChange}
        />
      </main>
      {modalVisible && (
        <Modal
          msg={modalContent}
          text1={modalActions.text1}
          text2={modalActions.text2}
          onCancel={modalActions.onCancel}
          onConfirm={modalActions.onConfirm}
        />
      )}
      {confirmModalVisible && (
        <ConfirmModal
          msg={confirmModalMessage}
          onConfirm={handleCloseConfirmModal}
        />
      )}
    </div>
  );
}

export default UserManageScreen;
